@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswald-regular.woff2"), url("../fonts/oswald-regular.woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswald-medium.woff2"), url("../fonts/oswald-medium.woff");
}
.page {
  overflow-x: hidden;
}

.page__body {
  position: relative;
  display: grid;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  grid-template-rows: auto 1fr auto;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  background-color: #ffffff;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}

.page__heading {
  margin: 28px 0 40px;
}
@media (min-width: 768px) {
  .page__heading {
    margin-top: 41px;
    margin-bottom: 45px;
  }
}
@media (min-width: 1360px) {
  .page__heading {
    margin-top: 47px;
  }
}

@media (min-width: 768px) {
  .page__form {
    margin-top: 54px;
  }
}

.preloader {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  display: grid;
  height: 100vh;
  grid-template-rows: repeat(4, 1fr);
  place-items: center;
  background-color: #ffffff;
  z-index: 10;
  overflow: hidden;
}
.preloader--nojs {
  animation-name: self-shutdown;
  animation-duration: 2s;
  animation-timing-function: steps(1, end);
  animation-fill-mode: forwards;
}

.preloader::after {
  content: "";
  grid-row: 2/3;
  width: 68px;
  height: 76px;
  background-image: url("../img/logo.svg");
  background-repeat: no-repeat;
  background-position: left center;
  background-size: cover;
  animation-name: soaring;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}
@media (min-width: 768px) {
  .preloader::after {
    width: 118px;
    height: 100px;
    background-image: url("../img/logo-tablet.svg");
  }
}
@media (min-width: 1360px) {
  .preloader::after {
    width: 142px;
    height: 118px;
    background-image: url("../img/logo-desktop.svg");
  }
}

@keyframes soaring {
  60% {
    transform: translateY(20%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes self-shutdown {
  100% {
    height: 0;
  }
}
@media (max-width: 767px) {
  .page-header {
    position: relative;
    padding: 11px 0 16px;
  }
}
@media (min-width: 768px) {
  .page-header {
    padding-top: 24px;
    padding-bottom: 24px;
  }
}
@media (min-width: 1360px) {
  .page-header {
    padding-top: 53px;
    padding-bottom: 29px;
  }
}

.page-header__container {
  display: grid;
  grid-template-columns: auto auto;
  justify-content: space-between;
}

.page-header__logo {
  outline: 2px solid transparent;
  outline-offset: 2px;
  transition-property: opacity, outline-color;
  transition-duration: 80ms;
}
.page-header__logo[href]:hover {
  opacity: 0.8;
}
.page-header__logo[href]:focus-visible {
  outline-color: #444444;
}
.page-header__logo[href]:active {
  opacity: 0.6;
}

@media (max-width: 359px) {
  .page-header__logo::after {
    content: "";
    position: absolute;
    top: 24px;
    left: 50%;
    width: 101px;
    height: 18px;
    background-image: url("../img/icons/cat-energy.svg");
    background-repeat: no-repeat;
    background-position: center;
    transform: translateX(-50%);
  }
}

.page-header__logo-image-wrapper {
  display: block;
}

.page-header__logo-image {
  vertical-align: middle;
  width: 151px;
  height: 38px;
}
@media (max-width: 359px) {
  .page-header__logo-image {
    width: 34px;
    object-fit: cover;
    object-position: left;
  }
}
@media (min-width: 768px) {
  .page-header__logo-image {
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1360px) {
  .page-header__logo-image {
    width: 202px;
    height: 59px;
  }
}

.page-header__toggler {
  position: relative;
  align-self: center;
  width: 24px;
  height: 20px;
  padding: 0;
  margin: 3px 0 0;
  border: none;
  background-color: transparent;
  background-image: url("../img/icons/burger.svg");
  background-repeat: no-repeat;
  background-position: center;
  outline: 2px solid transparent;
  outline-offset: 2px;
  transition-property: background-image, opacity, outline-color;
  transition-duration: 80ms;
  cursor: pointer;
}
.page-header__toggler::before {
  content: "";
  position: absolute;
  top: -10px;
  right: -10px;
  bottom: -10px;
  left: -10px;
}
.page-header__toggler--close {
  background-image: url("../img/icons/close.svg");
}
.page-header__toggler:hover {
  opacity: 0.6;
}
.page-header__toggler:focus-visible {
  outline-color: #444444;
}
.page-header__toggler:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .page-header__toggler {
    display: none;
  }
}

@media (max-width: 767px) {
  .page-header__navigation {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    background-color: #ffffff;
    transform: translateY(100%);
  }
}
@media (min-width: 768px) {
  .page-header__navigation {
    padding: 8px 0 0px 36px;
  }
}

@media (max-width: 767px) {
  .page-header__navigation--close {
    display: none;
  }
}

.page-header__navigation-list {
  margin: 0;
}

@media (max-width: 767px) {
  .page-header--nojs {
    position: static;
    padding-bottom: 0;
    overflow-x: hidden;
  }
  .page-header--nojs .page-header__toggler {
    display: none;
  }
  .page-header--nojs .page-header__navigation {
    position: static;
    grid-column: 1/-1;
    margin-top: 16px;
    transform: none;
  }
}
