  content: "";
  position: absolute;
  bottom: 0.7em;
  right: 0.9em;
  width: 20px;
  height: 21px;
  background-color: #222222;
  mask-repeat: no-repeat;
  mask-position: left center;
  transition-property: background-color;
  transition-duration: 80ms;
}
[class*=textfield--icon--]:focus-within::after {
